























































* {
	padding: 0;
	margin: 0;
}

html {
	font-family: verdana;
}

input, textarea {
	font-family: verdana;
	resize: none;
}

body {
	background-color: #E7EBF0;
}

::-webkit-scrollbar { 
		display: none; 
}

#header > .head {
	width: 1010px;
	height: 48px;
	background: #5682a3;
	left: 0;
	right: 0;
	position: fixed;
	top: 0;
	margin: auto;
}

#header > .head > .menu {
	width: calc(31% + 2px);
	float: left;
	color: #ffffff;
	height: 48px;
}

#header > .head > .menu:hover {
	background: #497799;
}

#header > .head > .menu > .icon {
	float: left;
	font-size: 35px;
	margin-left: 20px;
}

#header > .head > .menu > .title {
	padding: 15px 10px;
	margin-left: 65px;
	font-size: 14px;
}

#header > .head > .wrapper {
	margin-left: calc(31% + 2px);
	height: 48px;
	color: #ffffff;
}

#header > .head > .wrapper > .text {
	padding: 13px 10px;
}

#header > .head > .wrapper > .text > .lastseen {
	font-size: 13px;
	color: #b9cfe3;
}

#header > .head > .wrapper:hover {
	background: #497799;
}

#main {
	width: 1008px;
	height: calc(100vh - 70px);
	background-color: #ffffff;
	margin: 48px auto 0 auto;
	border-left: 1px solid #DFE5EC;
	border-right: 1px solid #DFE5EC;
}

#main > .sidebar {
	width: 31%;
	border-right: 2px solid #E9EBED;
	float: left;
	height: 100%;
}

#main > .sidebar > .search > .form {
	display: block;
	padding: 12px;
}

#main > .sidebar > .search > .form > input {
	width: calc(100% - 60px);
	font-size: 12px;
	line-height: normal;
	border: 1px solid #F2F2F2;
	border-radius: 2px;
	padding: 8px 26px 8px 30px;
	margin: 0;
	background-color: #F2F2F2;
	background-image: url(/static/img/IconsetW.1afbc80.png);
	background-repeat: no-repeat;
	background-position: -6px -205px;
	outline: none;
}

#main > .sidebar > .dialogs {
	height: calc(100% - 60px);
	overflow-y: scroll;
	margin-right: -2px;
}

#main > .sidebar > .dialogs > .dialog {
	padding: 7px 16px;
	font-size: 14px;
	overflow: hidden;
	border-right: 2px solid #E9EBED;
}

#main > .sidebar > .dialogs > .dialog:hover {
	background-color: #F2F6FA;
	cursor: pointer;
}

#main > .sidebar > .dialogs > .dialog.active > .time {
	color: #ffffff;
}

#main > .sidebar > .dialogs > .dialog.active,
#main > .sidebar > .dialogs > .dialog.active:hover {
	background-color: #6490b1;
	color: #ffffff;
	border-right: none;
}

#main > .sidebar > .dialogs > .dialog > .image {
	float: left;
}

#main > .sidebar > .dialogs > .dialog > .image > img {
	width: 48px;
	height: 48px;
	border-radius: 50%;
}

#main > .sidebar > .dialogs > .dialog > .data {
	margin-left: 60px;
	padding-right: 20px;
}

#main > .sidebar > .dialogs > .dialog > .data > .username {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-weight: 700;
	margin-top: 2px;
	margin-bottom: 2px;
}

#main > .sidebar > .dialogs > .dialog > .data > .message {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	width: 100%;
	margin-top: 5px;
}

#main > .sidebar > .dialogs > .dialog > .time {
	float: right;
	color: #b8d1e4;
	font-size: .85em;
}

#main > .content {
	width: calc(100% - 31%);
	height: calc(100vh - 70px);
	margin-left: 31%;
}

#main > .content > .bottom_form {
	width: 700px;
	position: absolute;
	bottom: 20px;
	background-color: #ffffff;
}

#main > .content > .bottom_form > .send_form {
	max-width: 554px;
	margin: 0 auto;
	padding: 21px 0;
	left: 0;
	right: 0;
	display: block;
}

#main > .content > .bottom_form > .send_form > .avatar {
	width: 52px;
	height: 52px;
}

#main > .content > .bottom_form > .send_form > .avatar > img {
	width: 52px;
	height: 52px;
	border-radius: 50%;
}

#main > .content > .bottom_form > .send_form > .avatar.my {
	float: left;
	margin-left: 13px;
}

#main > .content > .bottom_form > .send_form > .avatar.other {
	margin-right: 13px;
	float: right;
}

#main > .content > .bottom_form > .send_form > .form {
	margin-left: 70px;
	margin-right: 70px;
	padding: 0 10px;
}

#main > .content > .bottom_form > .send_form > .form > textarea {
	width: 100%;
	height: 60px;
	border: none;
	border-bottom: 2px solid #d2dbe3;
	outline: none;
}

#main > .content > .bottom_form > .send_form > .form > textarea:focus {
	border-bottom: 2px solid #77B7E4;
}

#main > .content > .bottom_form > .send_form > .form > .line {
	overflow: hidden;
	padding: 3px;
}

#main > .content > .bottom_form > .send_form > .form > .line > .left {
	float: left;
}

#main > .content > .bottom_form > .send_form > .form > .line > .left > .icon {
	display: inline-block;
	font-size: 22px;
	margin-right: 10px;
	color: #B5B5B5;
}

#main > .content > .bottom_form > .send_form > .form > .line > .left > .icon:hover,
#main > .content > .bottom_form > .send_form > .form > .line > .left > .icon:active {
	cursor: pointer;
}

#main > .content > .bottom_form > .send_form > .form > .line > .send {
	float: right;
	font-weight: bold;
	color: #72a0c7;
	font-size: 12px;
	margin-top: 7px;
}

#main > .content > .bottom_form > .send_form > .form > .line > .send:hover,
#main > .content > .bottom_form > .send_form > .form > .line > .send:focus {
	color: #499dd9;
}

#main > .content > .bottom_form > .send_form > .form > .line > .send > i {
	font-size: 1.1em;
}

#main > .content > .messages {
	width: 90%;
	height: calc(100vh - 40px - 170px);
	margin: 0 auto;
	overflow: hidden;
	overflow-y: scroll;
}

#main > .content > .messages > .message {
	display: block;
	overflow: hidden;
	margin-top: 15px;
}

#main > .content > .messages > .message > .select {
	float: left;
	width: 50px;
	height: 50px;
}

#main > .content > .messages > .message > .data {
	margin-left: 60px;
	overflow: hidden;
}

#main > .content > .messages > .message > .data > .image {
	float: left;
	width: 42px;
	height: 42px;
}

#main > .content > .messages > .message > .data > .image > img {
	border-radius: 50%;
	width: 42px;
	height: 42px;
}

#main > .content > .messages > .message > .data > .time {
	float: right;
	color: #adadad;
	font-size: .85em;
}

#main > .content > .messages > .message > .data > .data {
	margin-left: 50px;
}

#main > .content > .messages > .message > .data > .data > .username,
#main > .content > .messages > .message > .data > .data > .reply > .message > .username {
	color: #3a6d99;
}

#main > .content > .messages > .message > .data > .data > .username:hover,
#main > .content > .messages > .message > .data > .data > .reply > .message > .username:hover {
	color: #3a6d99;
	text-decoration: underline;
	cursor: pointer;
}

#main > .content > .messages > .message > .data > .data > .reply {
	margin: 10px 5px;
	border-left: 2px solid #7eaad1;
	padding-left: 10px;
}

#main > .content > .messages > .message > .data > .data > .reply > .message {
	margin: 5px 0;
}

#modal {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}

#modal > .modals {
	width: 100%;
	height: 100%;
	background: rgba(26,26,26,.7);
}

#modal > .modals > .modal {
	display: block;
	position: absolute;
	margin: auto;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: #ffffff;
}

#modal > .modals > .modal.profile {
	width: 480px;
	top: 25%;
	bottom: 25%;
}

#modal > .modals > .modal.profile > .header {
	background: #5580a3;
	padding: 26px;
}

#modal > .modals > .modal.profile > .header > .title {
	float: left;
	color: #ffffff;
	font-weight: 700;
	font-size: 12px;
}

#modal > .modals > .modal.profile > .header > .close {
	float: right;
	color: #dde8f1;
	font-size: 12px;
	font-weight: 700;
}

#modal > .modals > .modal.profile > .header > .title,
#modal > .modals > .modal.profile > .header > .close {
	overflow: hidden;
}

#modal > .modals > .modal.profile > .header > .profile {
	padding: 60px 0 25px 0;
}

#modal > .modals > .modal.profile > .header > .profile > .image {
	float: left;
}

#modal > .modals > .modal.profile > .header > .profile > .image > img {
	border-radius: 50%;
	width: 72px;
	height: 72px;
}

#modal > .modals > .modal.profile > .header > .profile > .info {
	margin-left: 90px;
}

#modal > .modals > .modal.profile > .header > .profile > .info > div {
	margin-top: 15px;
}

#modal > .modals > .modal.profile > .header > .profile > .info > .username {
	color: #ffffff;
	font-size: 14px;
	font-weight: 700;
}

#modal > .modals > .modal.profile > .header > .profile > .info > .status {
	color: #ccdeed;
	font-size: 12px;
}

#modal > .modals > .modal.profile > .actions {
	margin-top: -25px;
	text-align: right;
	padding: 0 50px;
}

#modal > .modals > .modal.profile > .actions > .action {
	margin-left: 20px;
	display: inline-block;
	width: 30px;
	height: 30px;
	padding: 11px;
	border-radius: 50%;
	background-color: #ffffff;
	color: #8C8C8C;
	border-bottom: 1px solid #d9d9d9;
	font-size: 1.5em;
}

#modal > .modals > .modal.profile > .actions > .action > i {
	float: left;
	margin-left: 5px;
}

#modal > .modals > .modal.profile > .info > .item {
	display: block;
}

#modal > .modals > .modal.profile > .info > .item > .icon {
	width: 120px;
	padding: 10px 0;
	float: left;
	text-align: center;
	font-size: 1.5em;
	color: #8C8C8C;
}

#modal > .modals > .modal.profile > .info > .item > .data {
	display: inline-block;
	padding: 10px 0;
	border-bottom: 1px solid #d9d9d9;
	width: calc(100% - 120px);
}

#modal > .modals > .modal.profile > .info > .item > .data > div {
	display: block;
	margin-bottom: 3px;
	font-size: 12px;
}

#modal > .modals > .modal.profile > .info > .item > .data > .name {
	color: #909090;
	font-size: 11px;
}

#modal > .modals > .modal.profile > .info > .item > .data > .menu {
	padding: 0;
}

#modal > .modals > .modal.profile > .info > .item > .data > .menu > a.item {
	display: block;
	margin: 9px 0 20px 0;
}

#auth > .header {
	background: #5682a3;
	height: 226px;
}

#auth > .content {
	width: 404px;
	margin: 0 auto;
	margin-top: -141px;
	display: block;
}

#auth > .content > .menu {
	font-size: 14px;
	padding: 22px 10px;
	color: #ffffff;
	overflow: hidden;
}

#auth > .content > .menu > .logo {
	float: left;
}

#auth > .content > .menu > .button {
	float: right;
	font-weight: 700;
}

#auth > .content > .menu > .button:hover {
	cursor: pointer;
}

#auth > .content > .block {
	background: #ffffff;
	border-radius: 2px;
	padding: 44px 65px;
}

#auth > .content > .block > .title {
	font-weight: 700;
}

#auth > .content > .block > .desc {
	margin: 20px 0 30px 0;
	font-size: 13px;
	color: #999;
}

